import { Directive, ElementRef, Input,HostListener } from '@angular/core';
import {Task} from './task';


@Directive({
  selector: '[myTask]'
})
export class MyTaskDirective {

  private _myTask :Task;

  constructor(private el: ElementRef ) {
        this.el.nativeElement.innerHTML += "conteudo inserido";
   }

   get myTask(){
   	return this._myTask;
   }
   @Input()
   set myTask(task:Task){
      this._myTask = task;
      this.chageColorTask();
   }
   
   //myTask : string;

   @HostListener('click')
    onClick(){
    	alert(this.myTask)
    }  

    chageColorTask(){
        this.el.nativeElement.style.color = this.myTask.value > 5 ? 'red' : 'green';
    }

}



